        10: { nan: [14.4, 19.5, 22.4], nv: [14.0, 19.9, 22.0] },
        11: { nan: [14.9, 20.2, 23.5], nv: [14.3, 21.0, 23.2] },
        12: { nan: [15.4, 20.9, 24.6], nv: [14.7, 21.8, 24.4] },

        13: { nan: [15.9, 21.8, 25.6], nv: [15.3, 22.5, 25.5] },
